@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2"), url("../fonts/opensans.woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2"), url("../fonts/opensansbold.woff");
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: normal;
  letter-spacing: normal;
  text-align: left;
  color: #444444;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  font-family: inherit;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  display: block;
  width: 100%;
  height: 52px;
  box-sizing: border-box;
  padding: 14px 0;
  text-align: center;
  border: 2px solid #444444;
  background-color: #ffffff;
  cursor: pointer;
}
.button:hover {
  background-color: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--green {
  color: #ffffff;
  border: none;
  background-color: #63d1bb;
}
.button--green:hover {
  background-color: #59d5bc;
}
.button--green:active {
  color: rgba(255, 255, 255, 0.3);
}
.toggle ~ label {
  font-family: inherit;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  color: #444444;
  text-transform: uppercase;
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  border: 2px solid rgba(68, 68, 68, 0.1);
}
.toggle:focus ~ label {
  outline: 2px solid rgba(100, 0, 130, 0.5);
  outline-offset: 2px;
}
.toggle:hover ~ label {
  border-color: rgba(68, 68, 68, 0.3);
}
.toggle:checked ~ label {
  border-color: #63d1bb;
}
.dividing-line {
  margin: 0;
  padding: 0;
  max-width: 1050px;
  margin: 0 auto;
  height: 7px;
  border: none;
  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-size: contain;
  transform: rotate(180deg);
}
@media (min-width: 768px) {
  .dividing-line {
    height: 17px;
  }
}
@media (min-width: 1150px) {
  .dividing-line {
    height: 24px;
  }
}
@media (min-width: 768px) {
