.main-nav__toggle {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 2;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav .main-nav__toggle::before,
.main-nav .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 40%;
  left: -20%;
  width: 27px;
  height: 2px;
  background-color: #000000;
}
.main-nav .main-nav__toggle::before {
  transform: rotate(43deg);
}
.main-nav .main-nav__toggle::after {
  transform: rotate(-43deg);
}
.main-nav--closed .main-nav__toggle::before {
  top: 2px;
  left: -1px;
  width: 20px;
  height: 2px;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  transform: none;
}
.main-nav--closed .main-nav__toggle::after {
  display: none;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__user-nav,
  .main-nav--closed .main-nav__search-nav {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__user-nav,
  .main-nav--nojs .main-nav__search-nav {
    display: block;
  }
}
