.user-nav__link--search {
  position: relative;
}
.user-nav__link--search::before {
  content: "";
  position: absolute;
  top: 31px;
  left: 10%;
  width: 12px;
  height: 12px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}
.user-nav__link--search::after {
  content: "";
  position: absolute;
  top: 45px;
  left: calc(10% + 13px);
  width: 6px;
  height: 2.5px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 0 40% 40% 0;
  transform: rotate(45deg);
}
.user-nav__link--search:hover::before {
  border-color: #63d1bb;
}
.user-nav__link--search:hover::after {
  background-color: #63d1bb;
}
.user-nav__link--search:active::before,
.user-nav__link--search:active::after {
  opacity: 0.3;
}
@media (min-width: 321px) {
  .user-nav__link--search::before {
    left: 15%;
  }
  .user-nav__link--search::after {
    left: calc(15% + 13px);
  }
}
@media (min-width: 431px) {
  .user-nav__link--search::before {
    left: 25%;
  }
  .user-nav__link--search::after {
    left: calc(25% + 13px);
  }
}
@media (min-width: 631px) {
  .user-nav__link--search::before {
    left: 30%;
  }
  .user-nav__link--search::after {
    left: calc(30% + 13px);
  }
}
