.about-us__wrapper {
  .page-container();
  padding-top: 27px;
  padding-bottom: 14px;

  @media (min-width: @tablet-width) {
    padding-top: 48px;
    padding-bottom: 27px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 61px;
    padding-bottom: 34px;
    padding-left: 225px;
  }
}

.about-us__title {
  .get-font-title-h2();
  .margin-padding-reset();
  margin-bottom: 32px;

  @media (min-width: @tablet-width) {
    .get-font-title-h2-tablet();
    margin-bottom: 50px;
    margin-left: -4px;
  }

  @media (min-width: @desktop-width) {
    .get-font-title-h2-desktop();
    margin-bottom: 48px;
  }
}

.about-us__wrapper-list {
  @media (min-width: @tablet-width) {
    display: flex;
  }
}

.about-us__list {
  .ul-style-reset();

  @media (min-width: @tablet-width) {
    width: 50%;
  }

  @media (min-width: @desktop-width) {
    width: 40%;
  }
}

.about-us__item {
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    margin-bottom: 57px;
  }
}

.about-us__icon--eko {
  margin-left: 8px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-left: 9px;
  }
}

.about-us__icon--money {
  margin-bottom: 20px;
}

.about-us__icon--heart {
  margin-top: 4px;
  margin-left: 7px;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    margin-top: 6px;
    margin-left: 8px;
  }
}

.about-us__icon--roll {
  margin-top: 3px;
  margin-left: 3px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-bottom: 9px;
  }
}

.about-us__icon--rocket {
  margin-top: 3px;
  margin-left: -6px;
  margin-bottom: 21px;

  @media (min-width: @tablet-width) {
    margin-top: 4px;
    margin-bottom: 20px;
  }
}

.about-us__icon--box {
  margin-left: 3px;
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-top: 3px;
    margin-bottom: 13px;
  }
}

.about-us__text {
  .margin-padding-reset();
  min-height: 40px;
  padding-right: 10px;

  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  color: @black;

  @media (min-width: @tablet-width) {
    min-height: 44px;
    padding-right: 30px;

    font-size: 20px;
    line-height: 22px;
  }
}
